import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import foto1 from "../../images/man-person-people-emotions-1990.jpg"

const Ubuntu = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title=" Cómo puede afectar la vitamina D a su estado de ánimo"
                meta="La vitamina D tiene muchas funciones en el cuerpo, incluyendo el mantenimiento de una buena salud ósea, cerebral y cardíaca. De hecho, hay pruebas que sugieren que los niveles más bajos de vitamina D están asociados con la depresión."
                description="La vitamina D tiene muchas funciones en el cuerpo, incluyendo el mantenimiento de una buena salud ósea, cerebral y cardíaca. De hecho, hay pruebas que sugieren que los niveles más bajos de vitamina D están asociados con la depresión."
                lang="es"
            />
            <h1>La vitamina D y la depresión: Cómo puede afectar la vitamina D a su estado de ánimo</h1>
            <hr/>
            <p class="introductionText">La vitamina D tiene muchas funciones en el cuerpo, incluyendo el mantenimiento
                de una buena salud ósea, cerebral y cardíaca. De hecho, hay pruebas que sugieren que los niveles más
                bajos de vitamina D están asociados con la depresión. </p>
            <hr/>
            <p></p>
            <div>
                <p></p>
                <p>Sin embargo, eso no significa que obtener m&aacute;s vitamina D por s&iacute; sola pueda prevenir o
                    tratar la depresi&oacute;n. Esto es lo que necesita saber sobre la relaci&oacute;n entre la vitamina
                    D y la depresi&oacute;n.</p>
                <p></p>
                <h2>Deficiencia de vitamina D y depresi&oacute;n&nbsp;</h2>
                <p>La deficiencia de vitamina D puede contribuir a la depresi&oacute;n. Por ejemplo, un examen de 13
                    estudios con m&aacute;s de 31.000 participantes determin&oacute; que las personas con una
                    deficiencia de vitamina D ten&iacute;an un mayor riesgo de sufrir depresi&oacute;n, en
                    comparaci&oacute;n con las que ten&iacute;an niveles m&aacute;s altos de vitamina D.&nbsp;</p>
                <p></p>
                <p>&quot;Los bajos niveles de vitamina D est&aacute;n asociados con la depresi&oacute;n mayor y menor,
                    as&iacute; como con los trastornos del estado de &aacute;nimo y el declive cognitivo m&aacute;s
                    r&aacute;pido&quot;, dice Robin Foroutan, MS, RDN, HHC, dietista de medicina integral y portavoz de
                    la Academia de Nutrici&oacute;n y Diet&eacute;tica.&nbsp;</p>
                <p></p>
                <div style={{textAlign:`center`,margin:'50px'}}>
                    <img src={foto1} style={{width:`50vh`}} alt="estados de animo"></img>
                </div>
                <p><b>Los bajos niveles de vitamina D pueden perjudicar la funci&oacute;n cognitiva porque hay receptores
                    de vitamina D en &aacute;reas del cerebro que son responsables del estado de &aacute;nimo y el
                    comportamiento, incluyendo el desarrollo de la depresi&oacute;n.</b></p>
                <p></p>
                <p>El adulto medio necesita alrededor de 600 a 800 unidades internacionales (UI) de vitamina D al
                    d&iacute;a. Como referencia, una porci&oacute;n de salm&oacute;n contiene aproximadamente 400 UI.
                    Sin embargo, pocos alimentos tienen naturalmente suficiente vitamina D para obtener su dosis
                    diaria.&nbsp;</p>
                <p></p>
                <p>De hecho, la forma m&aacute;s com&uacute;n de obtenerla es a trav&eacute;s de la luz solar, ya que la
                    vitamina D es sintetizada por la piel en respuesta a la luz ultravioleta. Debe tratar de obtener
                    alrededor de 15 minutos de luz solar entre las 10 a.m. y las 3 p.m., tres veces por semana, para
                    obtener suficiente vitamina D.&nbsp;</p>
                <p></p>
                <h2>&iquest;Conseguir m&aacute;s vitamina D ayuda con la depresi&oacute;n?</h2>
                <p>Mejorar los niveles de vitamina D puede ayudar a promover la salud general y una mayor
                    sensaci&oacute;n de bienestar. Sin embargo, las investigaciones no han demostrado que obtener
                    m&aacute;s vitamina D sea un tratamiento independiente suficiente para la depresi&oacute;n.</p>
                <p></p>
                <p>Por ejemplo, un estudio del 2014 publicado en el Journal of Clinical Endocrinology and Metabolism
                    encontr&oacute; que no hab&iacute;a ning&uacute;n efecto cuando la vitamina D se complementaba con
                    4.000 IUs en aquellos con depresi&oacute;n en comparaci&oacute;n con un placebo.&nbsp;</p>
                <p></p>
                <p>Y una revisi&oacute;n de 2019 de pacientes con depresi&oacute;n que se suplementaron con 70
                    microgramos (aproximadamente 2.800 UI) de vitamina D3 tampoco encontr&oacute; ning&uacute;n cambio
                    despu&eacute;s de la suplementaci&oacute;n.</p>
                <p></p>
                <p>&quot;No tenemos suficiente evidencia para recomendar esto como un tratamiento de primera
                    l&iacute;nea para la depresi&oacute;n&quot;, dice Dorothy Sit, MD, una psiquiatra de Northwestern
                    Medicine.&nbsp;</p>
                <p></p>
                <p>En cambio, su m&eacute;dico probablemente recomendar&aacute; una combinaci&oacute;n de terapia y
                    medicamentos como el primer paso para tratar la depresi&oacute;n. Tomado junto con algunos cambios
                    clave en el estilo de vida, como el ejercicio regular y comer saludablemente, usted podr&iacute;a
                    ser capaz de reducir los s&iacute;ntomas de la depresi&oacute;n.&nbsp;</p>
                <p></p>
                <h2>&iquest;Qu&eacute; alimentos tienen vitamina D</h2>
                <p>Aunque la vitamina D no solucione la depresi&oacute;n por s&iacute; sola, es importante incorporar
                    m&aacute;s vitamina D en la dieta si se tiene una deficiencia.&nbsp;</p>
                <p></p>
                <p>La vitamina D se encuentra en pocos alimentos de forma natural y en cantidades relativamente
                    peque&ntilde;as. En este <a className="link" href="/vitaminaD/#alimentos">enlace</a> podrás encontrar la fuentes de vitamina D mas importantes.</p>
                <p></p>

                <p></p>
                <p></p>

            </div>

        </Layout>
    )
}

export default Ubuntu
